		onDisconnect() {
			this.connected = false;
		},
	},
	components: {
		ConnectionButton,
		FollowButton,
	},
	data() {
		return {
			followed: false,
			connected: false,
		};
	},
	props: {
		userId: {
			default: undefined,
			required: true,
		},
		pageId: {
			default: undefined,
			required: true,
		},
		btnStyle: {
			type: String,
			default: "",
		},
		vertical: {
			type: Boolean,
			default: false,
		},
	},
};
</script>
